body{
    background-color: #fff;
}
.homepage{
    width: 100%;
    height: 100vh;
    position: relative;
}


.homepage-mid{
    width: 100%;
    height: 70vh;
    position: relative;
}

.homepage-mid h1{
    padding-top: 4rem !important;
}

.Background-page{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.homepage-text{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
}

.homepage-text h1{
    font-size: 4rem;
    font-weight: 600;
    background: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.homepage-text p{
    font-size: 1.3rem;
    color: #fff;
    padding: 0.5rem 0 2rem 0;
}

.homepage-text .show{
    text-decoration: none;
    background: #fff;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #222;
}

.homepage-text .hide{
    display: none;
}

.homepage-text .show:hover{
    background-color: #222;
    color: #fff;
}

@media screen and (max-width: 555px){
    .homepage-text h1{
        padding: 10px 20px;
    }

    .homepage-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;

    }

    .homepage-text .show{
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
    }
}