.ET{
    margin: 7rem 6rem;
    color: #2a2a2a;
    text-decoration: none;
}

.ET h1{
    font-size: 3rem;
}

.ETcard{
    margin-top: 3rem;
    display: block;
    display: inline-flex;
    width: 25%;
    justify-content: space-between;
    text-align: center;
    justify-content: center;
    
}

.et-card{
    text-align: start;
    box-shadow: 0 5px 25px 2px rgba(0, 0, 0, 0..);
    border-radius: 7px;
    cursor: pointer;
    padding: 1rem .8rem;
}

.et-image{
    height: 200px;
    overflow: hidden;
    border-radius: 7px;
}

.et-image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}

.et-image:hover img{
    transform: scale(1.3);

}

.et-card h4{
    font-size: 1.3rem;
    padding: .9rem 0 .2rem 0;
}

@media screen and (max-width: 850px){
    .ET{
        margin: 4rem 2rem;
    }

    .ETcard{
        flex-direction: column;
    }

    .et-card{
        width: 100%;
        margin-bottom: 1.5rem;
    }
}