.SignIn-Container {
    background-image: url("../assets/img/bg-login.jpg");
    background-size: cover;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    
    .SignUp-SubContainer {
        height: 70%;
        width: 450px;
        border: 0.5px solid lightgray;
        border-radius: 10px;
        padding: 40px;
        margin-top: 10%;
        margin-bottom: 10%;
        background-color: rgba(252, 248, 248, 0.7);


        .Header {
            height: 15%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            font-family: 'Roboto';
            font-size: 25px;
            font-weight: 500;
        }

        .Body {
            height: 70%;
            width: 100%;
            // border: 0.5px solid lightgray;
            // box-sizing: border-box;

            .form{
                height: 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                .TextField{
                    width: 300px;
                    margin: 20px;  
                }
            }
            .back{
                position: relative;
                margin-top: -22%;
                margin-left: 9%;
                font-size: 15px;
                list-style-type: none;
            }
            .back a, i{
                color: #3F51B5;
                text-decoration: none;
            }
            .forgot{
                position: relative;
                margin-top: -6.5%;
                margin-left: 55%;
                font-size: 15px;
            }
            .forgot a{
                color: #3F51B5;
                text-decoration: none;
            }
        }

        .Buttons {
            height: 15%;
            width: 100%;
            // border: 0.5px solid lightgray;
            display: flex;
            align-items: center;
            justify-content: space-around;

            .Btn{
                height: 35px;
                text-transform: none;
            }
        }
    }

    .SignUp-SubContainer:hover {
        box-shadow: 0 0 5px -2px black;
    }
}