.NavbarItems{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, .25);
    width: 100%;
    height: 65px;
    border-radius: 13px;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    z-index: 9999;
    background-color: #fff;
    top: 0.5%
}

.navbar-logo img{
    position: relative;
    max-width: 170px;
}

.navbar-menu{
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
    margin-top: 1%;
}

.navbar-links{
    text-decoration: none;
    color: #222;
    font-size: 1rem;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.navbar-links:hover{
    background-color: #004156;
    color: #fff;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}

.navbar-links-logout{
    color: #222;
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.navbar-links-logout:hover{
    background-color: #222;
    color: #fff;
}

.menu-icons{
    display: none;
    cursor: pointer;
}

@media screen and (max-width: 850px){
    .NavbarItems{
        z-index: 99;
    }
    .navbar-menu{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: #fff;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -100%;
        opacity: 0;
        align-items: stretch;
        padding: 80px 0 30px 0;
        margin: 0;  
        transition:  0.3s ease-in-out;    
    }

    .navbar-menu.active{
        left: 0;
        opacity: 1;
        z-index: -1;
        transition:  0.3s ease-in-out;
    }

    .navbar-links{
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
    }

    .navbar-links:hover{
        background: #2398AB;
        transition: none;
    }

    button{
        display: none;
    }

    .navbar-links-sign{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: auto;
        border-radius: 4px;
        width: 80%;
        background: #2398AB;
        text-decoration: none;
        color: #fff;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
    }

    .menu-icons{
        display: block;
    }

    .menu-icons i{
        font-size: 1.2rem;
        color: #222;
    }
}